@import 'theme';

.header--div-container {
  display: flex;
  align-items: row;
  justify-content: space-between;
  padding: 20px;
  background-image: linear-gradient($primary-color, $secondary-color);
  // background-color: $blue;
}

.header--div-menu {
  display: inline-flex;
  align-items: row;
  justify-content: space-evenly;
  color: lightblue;
  * {
    text-decoration: none;
    padding: 10px;
  }
  a {
    border: 1px solid blue;
    border-radius: 50px;
    color: inherit;
    background-color: $blue;
  }
}

.header--h1-title {
  padding: 10px;
  font-size: 20px;
  color: lightblue;
}
